.skill{
    padding:0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-top: 4rem;
}
/*right side*/
.skill-right{
    position: relative;
    flex: 1;
}
.skill-right>*{
    position: absolute;
    z-index:1;
}
.main-circle{
    left:10rem;
    position: relative;
    width: 18rem;
    height: 18rem;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    top:2rem;
    background: white;
}
.main-circle>*{
    position: absolute;
}
.main-circle>:nth-child(1){
    top: -3rem;
    left: 6rem;
}
.main-circle>:nth-child(2){
    left: -3rem;
    top: 5rem;
}
.main-circle>:nth-child(3){
    top: 6rem;
    left: 6rem;
}
.main-circle>:nth-child(4){
    left: 15rem;
    top: 6rem;
}
.main-circle>:nth-child(5){
    top: 14rem;
    left: 6rem;
}
.sec-circle {
    width: 5rem;
    height: 5rem;
    position: absolute;
    border-radius: 100%;
    border: 5px solid #ebebeb;
    display: flex;
    align-items: center;
    background: "none";
    box-shadow: var(--smboxShadow);
    background: white;
}

.sec-circle>img {
    transform: scale(0.5);
    width: 100%;
    height: 100%;
    object-fit: cover; 
    object-position: center center; 
}
.back-circle{
    position:absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;
    z-index: -1;
}
.blueCircle{
    background: #1949B8;
    left: 18rem;
    top: 0rem;
}
.yellowCircle{
    background: #F5C32C;
    left: 18rem;
    top: 8rem;
}
@media screen and (max-width:480px) {
    .skill{
        flex-direction: column;
        height: 30rem;padding: 0;
        gap: 5rem;
    }
    .skill-right{
        display: flex;
        align-items: flex-start;
        justify-content: start;
        transform: scale(0.7);
    }
    .main-circle{
        position: static;
    }
    .back-circle{
        left: 8rem!important;
    }
}