.img-about{
    position: relative;
}
.img-box{
    text-align: center;

}
.img-box img{
    width: 90%;
    height: auto;

}
.liquid-shape>svg{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
    top: 2%;
}


.about{
    padding:0 3rem 0 3rem;
    display: flex;
    height: 80vh;
    margin-bottom: 8rem;
    margin-top: 3rem;
}
.about-right{
    top: 6rem;
    display: flex;
    flex-direction: column;
    position: relative;
}
.about-right>:nth-child(1){
    color: var(--black);
    font-size: 2rem;
    font-weight: bold;
}
.about-right>:nth-child(2){
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}
.about-right>:nth-child(3){
    color: var(--gray);
    font-size: 14px;
    margin-top: 1rem;
}
@media screen and (max-width:480px) {
    .about{
        flex-direction: column;
        height: 40rem;
        margin-top: 0;
        gap: 0rem;
        padding: 0;
    }
    .liquid-shape>svg{
        top: 0;
        width: 30rem;
        height: 20rem;
        margin-left: -5rem;
    }
    .img-box{
        top: 0;
        text-align: center;
        height: 20rem;
        width: 20rem;
        right: 20rem;
        margin-left: -1rem;
        transform: scale(1.2);
       
    }
}