.contact-form{
    padding:0 3rem 0 3rem;
    display: flex;
    height: 70vh;
    margin-top: 4rem;
}
.c-right{
    display: flex;
    justify-content: center;
    position: relative;
    flex: 1;
}
.c-right>form{
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 2rem;
}
.c-right .user{
    width: 20rem;
    height: 2rem;
    padding: 0.3rem;
    outline: none;
    border: 2px solid var(--orange);
    border-radius: 8px;
    font-size: 16px;
}
textarea{
    height: 4rem!important;
}
@media screen and (max-width:480px) {
    .contact-form{
        margin-top: 0;
        flex-direction: column;
        gap: 5rem;
        height: 34rem;
        padding: 0;
    }
   
    .c-right .user{
        width: 15rem;
    }
}